<template lang='html'>
  <div class='wrapper-jexcel'>
	<select v-model="pilihan_id" @change="onChangeSatker" name="satker" id="satker" class="form-control">
        <option selected disabled value="">Pilih salah satu</option>
        <option v-for="(satker,satkerIndex) in listsatker" :key="satker.id" :value="satker.id">{{ satker.nama }}</option>
    </select>
    <!-- <span>Selected: {{ pilihan_satker }}</span> -->
    <br />
    <div id='doc-title' class="pl-2">
        <img id='its' align='left' hspace='10' src="./../../../assets/img/its_logo.png" width="100" height="100">
        <div id='title'>
            <h1>Indikator Kontrak Kinerja 2020</h1>
            <h3>Kepala Departemen XYZ</h3>
        </div>
    </div>
    <div id='spreadsheet' ref='spreadsheet' class="pl-2"></div>
  </div>
</template>

<script>
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import axios from 'axios'
// import Dropdown from './Konkin/Dropdown'

export default {
//   name: 'app',
//   components: {
//     Dropdown
//   },
  data() {
    return {
      pilihan_id: '',
      pilihan_satker: '',
      getidsatker: null,
      listsatker: []
    }
  },
  mounted() {
    this.dropping()
    this.load()
  },
  watch: {
    pilihan_satker: function (refreshpls) {
    //   console.log('helpmepls')
      document.getElementById('spreadsheet').innerHTML = ''
      this.load()
    }
  },
  methods: {
    load() {
      // console.log('hello? this is load')
      axios.get('https://10.199.14.46:8024/api/konkin/' + this.pilihan_satker).then(res => {
        // console.log(res.data)
        // console.log('i actually get the data to table!')
        var options = {
          // url: 'https://10.199.14.46:3000/indikator_satuankerja',
          data: res.data,
          editable: false,
          columns: [
            { type: 'text', title: 'Aspek', width: '100', name: 'aspek' },
            { type: 'text', title: 'Komponen Aspek', width: '200', name: 'komponen_aspek', wordWrap: true },
            { type: 'text', title: 'Nama', width: '200', name: 'nama', wordWrap: true },
            { type: 'numeric', title: 'Bobot', width: '60' },
            { type: 'numeric', title: 'Target', width: '60' },
            { type: 'text', title: 'Capaian', width: '100' }
          ]
        }
        let spreadsheet = jexcel(document.getElementById('spreadsheet'), options)
        Object.assign(this, { spreadsheet })
      })
    },
    dropping() {
      this.getidsatker = window.localStorage.getItem('iduser')
      if (window.localStorage.getItem('namauser').includes('Rektorat') || window.localStorage.getItem('namauser').includes('Direktorat')) {
        axios.get('https://10.199.14.46:8024/api/satuankerjadrop/').then(res => {
          this.listsatker = res.data
        })
      } else {
        axios.post('https://10.199.14.46:8024/api/satuankerjadrop/', {username: this.getidsatker}).then(res => {
          this.listsatker = res.data
        })
      }
    },
    onChangeSatker: function (event) {
      axios.get('https://10.199.14.46:8024/api/satuankerja/hasil/' + this.pilihan_id).then(res => {
        this.pilihan_satker = Object.values(res.data[0])[0]
        // console.log(this.pilihan_satker)
      })
    }
  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}
</style>



