<template>
  <li class="dropdown user user-menu">
    <a href="javascript:;" class="dropdown-toggle" data-toggle="dropdown">
      <!-- The user image in the navbar-->
      <img src="./../../assets/img/its_logo.png" class="user-image" alt="User Image">
      <!-- hidden-xs hides the username on small devices so only the image appears. -->
      <span class="hidden-xs">{{munculnama}}</span>
    </a>
    <!-- Account Info and Menu -->
    <ul class="dropdown-menu">
      <li class="user-header" style="height:auto;min-height:85px;padding-bottom:15px;">
        <p>
          <span>{{munculnama}}</span>
          <!-- <small v-for="role in user.roles" :key="role">{{role}}</small> -->
        </p>
      </li>
      <li class="user-footer">
        <div>
          <button v-if="apakahlogin != '' && apakahlogin != null" v-on:click="logoutme()">
            <i class="fa fa-sign-out"></i>
            <span>Logout</span>
          </button>
          <button v-else v-on:click="loginme()">
            <i class="fa fa-sign-in"></i>
            <span>Login</span>
          </button>
        </div>
      </li>
    </ul>
  </li>
</template>

<script>
import App from './../App'
export default {
  name: 'UserMenu',
  props: ['user'],
  data() {
    return {
      munculnama: window.localStorage.getItem('namauser') || 'Belum Login',
      apakahlogin: this.$router.app.$store.state.token || null
    }
  },
  components: {
    App
  },
  mounted() {
    console.log(this.apakahlogin)
    if (this.munculnama == null) {
      this.munculnama = 'Belum Login'
    }
  },
  methods: {
    logoutme: function () {
      // console.log('before')
      // console.log(this.$store)
      // console.log(window.localStorage)

      this.$store.commit('SET_USER', null)
      this.$store.commit('SET_TOKEN', null)

      if (window.localStorage) {
        window.localStorage.setItem('user', null)
        window.localStorage.setItem('token', null)
        window.localStorage.setItem('namauser', null)
        window.localStorage.setItem('iduser', null)
      }

      // console.log('after')
      // console.log(this.$store)
      // console.log(window.localStorage)

      this.$router.push('/login')
    },
    loginme: function () {
      this.$router.push('/login')
    }
  }
}
</script>



